.achieve {
  margin-bottom: 30px;
  margin-top: 10px;
  margin-left: 70px;
  margin-inline-start: 70px;
  display: grid;
  grid-template-columns: repeat(1, 1fr); /* create two columns of equal width */
  grid-template-rows: repeat(4, auto); /* create three rows of auto height */
  gap: 20px; /* add some gap between the cards */
  margin-inline-end: 30px;
}

.items1 {
  background-color: rgb(122, 122, 110);
  box-shadow: 0 4px 8px 0 rgba(224, 215, 215, 0.2); /* add some shadow to the cards */
  padding: 20px; /* add some padding to the cards */
  transition: transform 0.3s; /* add some transition effect to the cards */
  border-radius: 10px;
}

.items1:hover {
  transform: scale(1.1); /* make the cards slightly larger on hover */
}

.achieve1 {
  background-color: bisque;
  margin-left: 30px;
  margin-inline-start: 30px;
  border-radius: 10px;
}

.achieve1 h2 {
  margin-left: 30px;
}
