.work-container {
  margin-left: 40px;
  margin-right: 40px;
  margin-top: 30px;
  background-color: rgb(127, 128, 128);
  flex-direction: row;
  border-radius: 10px;
}

.work-list-container {
  border: 5px;
  padding: 20px;
}
.work-desc {
  max-width: 60%;
}
.fa-desktop {
  color: rgb(14, 217, 244);
}

.all-work {
  margin: 20px;
  border-radius: 16px;
  box-shadow: 0 4px 30 rgba(0, 0, 0, 0.1);
  padding: 20px;
}

.items {
  display: flex; /* added to make it a flex container */
  align-items: center;
}

.item-desc {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  margin: 20px;
  margin-left: 30px;
}

@media (min-width: 768px) {
  .work-list-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .work-desc {
    width: 40%;
  }
  .all-work {
    width: 60%;
  }
  .items {
    width: 80%;
  }
}

.work-desc p {
  font-size: 14px;
}
