nav {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgb(248, 234, 154);
  padding: 0 20px;
  border-radius: 20px;
  margin-left: 10px;
  margin-inline-start: 10px;
}

.nav-logo img {
  width: 90px;
  padding-top: 10%;
  border-radius: 60%;
}

nav ul {
  display: flex;
  list-style: none;
}

nav ul li {
  padding: 8px 15px;
  border-radius: 8px;
  transition: 0.3s;
}

nav ul li a {
  text-decoration: none;
  color: rgb(246, 143, 8);
}

nav ul li:hover {
  background-color: rgb(22, 50, 50);
  color: rgb(233, 91, 14);
  padding: 8px;
}
