footer {
  background-color: #111e38;
  color: aliceblue;
  padding: 20px;
  margin: 0 auto;
  margin-bottom: 30px;
  border-radius: 40px;
  max-width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}

footer img {
  width: 200px;
  border-radius: 30px;
}

/* Select the icons with the class fa */
.fa {
  /* Add some transition effect to the icons */
  transition: all 0.3s;
}

/* Select the icons with the class fa when they are hovered */
.fa:hover {
  /* Change the color of the icons to blue */
  color: blue;
  /* Increase the size of the icons by 50% */
  transform: scale(1.5);
}

.social-media {
  margin-top: 20px;
  margin-left: 30px;
}
