/* Styling for header container */
.header-container {
  border-radius: 10px;
  margin-left: 25px;
  margin-inline-start: 25px;
  margin-top: 30px;
  display: flex;
  flex-direction: row; /* Changed from column to row */
  align-items: center; /* Center items vertically */
  justify-content: space-between; /* Create space between items horizontally */
  background-color: rgb(246, 183, 120);
}

/* Styling for header content */
.header-content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  margin-top: 10px;
  margin-left: 30px;
}

/* Styling for header content headings */
.header-content h2 {
  font-size: 4rem;
  font-family: "Saira", sans-serif;
}

.header-content h5 {
  font-size: 1rem;
  line-height: 1.9;
  color: #1207db;
}

/* Styling for full name */
.FullName {
  font-size: 3rem;
  margin-top: 10px;
  color: #f613f6;
}

/* Styling for profile image */
.profile-img {
  margin-inline-end: 20px;
  position: relative;
  max-width: 30%;
}

.profile-img img {
  width: 100%;
  height: 100%;
  border-bottom: 10px solid #f0f408;
  border-left: 10px solid rgb(21, 9, 237);
  border-radius: 50%;
  object-fit: cover;
}
