p {
  font-size: 10px;
}

.works-list-conatiner {
  margin-bottom: 30px;
  margin-top: 10px;
  margin-left: 20px;
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* create two columns of equal width */
  grid-template-rows: repeat(3, auto); /* create three rows of auto height */
  gap: 20px; /* add some gap between the cards */
  margin-inline-end: 30px;
}

.work-item {
  background-color: rgb(183, 179, 123);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2); /* add some shadow to the cards */
  padding: 20px; /* add some padding to the cards */
  transition: transform 0.3s; /* add some transition effect to the cards */
  border-radius: 10px;
}

.work-item:hover {
  transform: scale(1.1); /* make the cards slightly larger on hover */
}

.works-container h2 {
  margin-left: 30px;
}

.works-container {
  margin-top: 30px;
}
